import * as util from '../../util/util'

export default {
  props: {
    date: {
      type: String
    },
    time: {
      type: String
    },
    cell: {
      type: Object,
      default: () => {
        return {
          img: '',
          title1: '',
          title2: '',
          oldPrice: 0,
          lowPrice: 0,
          vShopProductGuid: ''
        }
      }
    },
    isActive: {
      type: Boolean,
      default: false
    },
    showMinPrice: {
      type: Boolean,
      default: true
    },
    nowPrice: {
      type: Number,
      default: 0
    },
    chaPrice: {
      type: Number,
      default: 0
    },
    kanPrice: {
      type: Number,
      default: 0
    },
    user: {
      type: Object,
      default: () => {
        return {
          img: '',
          text: ''
        }
      }
    },
    textList: {
      type: Array,
      default: () => {
        return [
          {
            headImgUrl: '',
            message: '',
            deductPrice: 0
          }
        ]
      }
    },
    isKan: {
      type: Boolean,
      default: false
    },
    isHost: {
      type: Boolean
    },
    isShow: {
      type: Boolean,
      default: false
    },
    isShowShare: {
      type: Boolean,
      default: false
    },
    isWeixin: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      bgColor: 'bg-color'
    }
  },
  mounted () {
    util.setTitle('')
  },
  methods: {
    /*  */
    closeShow () {
      this.$emit('closeShow')
    },
    /*  */
    openShow () {
      this.$emit('openShow')
    },
    /*  */
    setShare () {
      this.$emit('setShare')
    },
    /*  */
    helpKan () {
      this.$emit('helpKan')
    },
    /*  */
    iWant () {
      this.$emit('iWant')
    },
    /*  */
    shareKan () {
      this.$emit('shareKan')
    },
    /*  */
    shareKanHelp (vShopProductGuid) {
      console.log('')
      this.$emit('shareKanHelp', vShopProductGuid)
    },
    /*  */
    toBuy () {
      this.$emit('toBuy')
    },
    /*  */
    toDetail (vShopProductGuid) {
      this.$emit('toDetail', vShopProductGuid)
    }
  }
}



